import request from './index';

/**
 *  GET   params 
 *  POST  data   
 * */

/**
 * 1
 * 			
 * name		string	
 * type		string	 1:
 * overtime		long	 864000001
 * content		String	
 * teid		String	ID
 * */
export function createTask(options) {
  let data = {
    name: options.name,
    type: options.type,
    teid: options.teid,
  };
  
  if (options.overtime) data.overtime = options.overtime;
  if (options.content) data.content = options.content;
  
  return request({
    url: '/task/createTask',
    method: 'post',
    data
  });
}

/**
 * 2
 * 			
 * id		string	
 * */
export function deleteTask(options) {
  let data = {
    id: options.id,
  };
  
  return request({
    url: '/task/deleteTask',
    method: 'post',
    data
  });
}

/**
 * 3
 * 			
 * id		string	
 * content		String	
 * */
export function performTask(options) {
  let data = {
    id: options.id,
  };
  
  if (options.content) data.content = options.content;
  
  return request({
    url: '/task/performTask',
    method: 'post',
    data
  });
}

/**
 * 4
 * 			
 * id		string	
 * isDeleteCompleted		string	 0: 1:
 * */
export function taskStatus(options) {
  let data = {
    id: options.id,
  };
  
  if (options.isDeleteCompleted) data.isDeleteCompleted = options.isDeleteCompleted;
  
  return request({
    url: '/task/taskStatus',
    method: 'post',
    data
  });
}

/**
 * 5()
 * 			
 * kind		string	 3: 4:
 * name		string	
 * taskId		string	ID
 * content		string	
 * index		int	
 * */
export function uploadNonFile(options) {
  let data = {
    kind: options.kind,
    taskId: options.taskId,
    content: options.content,
    index: options.index,
    contentType: 'multipart',
  };
  
  if (options.name) data.name = options.name;
  
  return request({
    url: '/resources/uploadNonFile',
    method: 'post',
    data
  });
}
